

.advant {
    padding-top: 40px;
    padding-bottom: 60px;
    background-color: var(--bg-light-pink);

    @media screen and (min-width:768px) {
        padding-top: 60px;
        padding-bottom: 60px;
    }

     &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 32px;

        @media screen and (min-width:768px) {
            gap: 25px;           
        }

         @media screen and (min-width:768px) {
            gap: 100px;           
        }
     }
}





.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-left: 20px;
    padding-right: 20px;

    @media screen and (min-width: 320px) {
        max-width: 320px;
    }

    @media screen and (min-width: 768px) {
        max-width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }

    @media screen and (min-width: 1200px) {
        max-width: 1200px;
        padding-left: 16px;
        padding-right: 16px;
    }

}



.advant___item {
    max-width: 280px;

    @media screen and (min-width: 768px) {
        max-width: 170px;
    }

    @media screen and (min-width: 1200px) {
        max-width: 300px;
    }

}

.advant___item:nth-child(1) .advant__box {
    &::before {
        content: "";
        display: block;
        width: 33px;
        height: 50px;
        background-size: 33px 50px;

        background-image: url(../images/advant/advant-icon-1.png);

        @media (min-device-pixel-ratio: 2),
                (min-resolution: 192dpi),
                (min-resolution: 2dppx) {
        
                background-image: url(../images/advant/advant-icon@2x-1.png);
        
                }
    }
}

.advant___item:nth-child(2) .advant__box {
    &::before {
        content: "";
        display: block;
        width: 50px;
        height: 50px;
        background-size: 50px 50px;

        background-image: url(../images/advant/advant-icon-2.png);

        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {

            background-image: url(../images/advant/advant-icon@2x-2.png);

        }
    }
}

.advant___item:nth-child(3) .advant__box {
    &::before {
        content: "";
        display: block;
        width: 44px;
        height: 50px;
        background-size: 44px 50px;

        background-image: url(../images/advant/advant-icon-3.png);

        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {

            background-image: url(../images/advant/advant-icon@2x-3.png);

        }
    }
}



.advant__number {
    margin-top: 16px;

    font-family: var(--font-titan);
    font-size: 36px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    @media screen and (min-width: 768px) {
        margin-top: 36px;
        font-size: 48px;
        line-height: 1.15;
    }

    @media screen and (min-width: 1200px) {
        margin-top: 40px;
        font-size: 58px;
    }
    
    color: var(--brand-color);
}

.advant__txt {
    margin-top: 15px;

    font-weight: 700;
    font-size: 12px;
    line-height: 1.67;
    color: #000000;

    @media screen and (min-width: 768px) {
        margin-top: 13px;
        font-size: 14px;
        line-height: 1.71;
    }

     @media screen and (min-width: 1200px) {
        margin-top: 16px;
        font-size: 16px;
        line-height: 1.75;
    }
}

